import { getApiKey } from "./settings.js";

const GEMINI_MODEL = "gemini-2.5-flash-preview-04-17";
const DEFAULT_SYSTEM_PROMPT_PARTS = [
  // 
  "",
  "",
  "",
  "",
];

async function fetchArticleContentInternal(url, jinaApiKey) {
  if (!jinaApiKey) {
    return { error: "Jina API" };
  }
  const readerApiUrl = `https://r.jina.ai/${url}`;
  try {
    const response = await fetch(readerApiUrl, {
      method: "GET",
      headers: {
        Authorization: `Bearer ${jinaApiKey}`,
        Accept: "application/json",
      },
    });
    if (!response.ok) {
      const errorData = await response
        .json()
        .catch(() => ({ message: response.statusText }));
      return {
        error: `Jina API: ${errorData.message || response.status}`,
      };
    }
    const data = await response.json();
    if (data && data.data && data.data.content) {
      return { content: data.data.content, title: data.data.title || null };
    }
    return { error: "Jina API" };
  } catch (e) {
    console.error("fetchArticleContentInternal error:", e);
    return {
      error: "",
    };
  }
}

async function generateSummaryWithGeminiInternal(
  textToSummarize,
  userSettings,
  geminiApiKey
) {
  // internal
  if (!geminiApiKey) {
    return { error: "Gemini API" };
  }

  // ---  ---
  let promptParts = [...DEFAULT_SYSTEM_PROMPT_PARTS];

  // 
  if (userSettings.userLevel) {
    let levelDescription = "";
    switch (userSettings.userLevel) {
      case 1:
        levelDescription =
          "";
        break;
      case 2:
        levelDescription =
          "";
        break;
      case 3:
        levelDescription =
          "";
        break;
      case 4:
        levelDescription =
          "";
        break;
      case 5:
        levelDescription =
          "";
        break;
    }
    if (levelDescription) promptParts.push(levelDescription);
  }
  if (userSettings.userLevelText) {
    // LLM
    // 
    promptParts.push(
      `: ${userSettings.userLevelText}`
    );
  }

  // 
  if (userSettings.additionalPrompt) {
    // 
    promptParts.push(
      `: ${userSettings.additionalPrompt}`
    );
  }

  promptParts.push("\n---  ---");
  promptParts.push(textToSummarize);
  promptParts.push("---  ---");

  const finalPrompt = promptParts.join("\n");
  console.log("Final prompt to Gemini:", finalPrompt); // 

  // --- Gemini API  ---
  const geminiApiUrl = `https://generativelanguage.googleapis.com/v1beta/models/${GEMINI_MODEL}:generateContent?key=${geminiApiKey}`;
  const requestBody = { contents: [{ parts: [{ text: finalPrompt }] }] };

  try {
    const response = await fetch(geminiApiUrl, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });
    if (!response.ok) {
      const errorData = await response
        .json()
        .catch(() => ({ message: response.statusText }));
      return {
        error: `Gemini API: ${errorData.error ? errorData.error.message : response.status}`,
      };
    }
    const data = await response.json();
    if (
      data.candidates &&
      data.candidates.length > 0 &&
      data.candidates[0].content &&
      data.candidates[0].content.parts &&
      data.candidates[0].content.parts.length > 0
    ) {
      return {
        summaryMarkdown: data.candidates[0].content.parts[0].text,
        actualPromptSentToLLM: finalPrompt,
      };
    }
    return { error: "Gemini API" };
  } catch (e) {
    console.error("generateSummaryWithGeminiInternal error:", e);
    return { error: "" };
  }
}

/**
 * URL
 * @param {string} url - URL
 * @param {object} userSettings -  (settings.js)
 * @returns {Promise<object>} { title?: string, summaryMarkdown?: string, error?: string } 
 */
export async function getDocumentSummary(url, userSettings) {
  const jinaApiKey = getApiKey("jina"); // main.jsgetSettings()
  const geminiApiKey = getApiKey("gemini");

  // 1. 
  const articleResult = await fetchArticleContentInternal(url, jinaApiKey);
  if (articleResult.error) {
    return { error: articleResult.error };
  }
  if (!articleResult.content) {
    return { error: "" };
  }

  // 2. 
  const summaryResult = await generateSummaryWithGeminiInternal(
    articleResult.content,
    userSettings,
    geminiApiKey
  );
  if (summaryResult.error) {
    return { error: summaryResult.error, title: articleResult.title }; // 
  }

  return {
    title: articleResult.title,
    summaryMarkdown: summaryResult.summaryMarkdown,
    actualPromptSentToLLM: summaryResult.actualPromptSentToLLM,
  };
}

export async function getFollowUpResponse(
  conversationHistory,
  newQuestionText
) {
  const geminiApiKey = getApiKey("gemini"); // settings.js
  if (!geminiApiKey) {
    return { error: "Gemini API" };
  }

  // timestamp
  // conversationHistory  [{role, parts:[{text}]}, ...] 
  const contentsForApi = conversationHistory.map((turn) => ({
    role: turn.role,
    parts: turn.parts.map((part) => ({ text: part.text })), // textpart
  }));
  contentsForApi.push({ role: "user", parts: [{ text: newQuestionText }] });

  // --- () :  contentsForApi  ---
  // : const limitedContents = limitConversationTurns(contentsForApi, 10); // 10
  // finalContents = limitedContents;

  // ---  () ---
  //  conversationHistory 
  // Gemini API
  // userSettings
  // contentsForApi  ()  model 
  //  + 

  const requestBody = {
    contents: contentsForApi,
    // generationConfig  safetySettings  userSettings 
    generationConfig: {
      /* ... */
    },
    safetySettings: [
      /* ... */
    ],
  };
  // 
  console.log(
    "Sending to Gemini (follow-up):",
    JSON.stringify(requestBody, null, 2)
  );

  const geminiApiUrl = `https://generativelanguage.googleapis.com/v1beta/models/${GEMINI_MODEL}:generateContent?key=${geminiApiKey}`;
  try {
    const response = await fetch(geminiApiUrl, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(requestBody),
    });
    if (!response.ok) {
      const errorData = await response
        .json()
        .catch(() => ({ message: response.statusText }));
      return {
        error: `Gemini API (): ${errorData.error ? errorData.error.message : response.status}`,
      };
    }
    const data = await response.json();
    if (
      data.candidates &&
      data.candidates.length > 0 &&
      data.candidates[0].content &&
      data.candidates[0].content.parts &&
      data.candidates[0].content.parts.length > 0
    ) {
      // 
      return {
        userQuestion: newQuestionText, // 
        modelResponseMarkdown: data.candidates[0].content.parts[0].text,
      };
    }
    return {
      error: "Gemini API ()",
    };
  } catch (e) {
    console.error("getFollowUpResponse error:", e);
    return {
      error: "",
    };
  }
}
